<template>
  <div class="the-affiliate" :class="[ `the-affiliate__${theme}` ]">
    <transition-content>
      <div class="the-affiliate__program" v-if="!statisticWindow">
        <e-bottom-layout :borderTop="true">
          <template slot="body">
            <e-heading
              title="Affiliate Program"
              subtitle="Get 50% from our profit. Automatic &amp; Lifetime Payouts."
              :icon="affiliateIcon"
            >
            </e-heading>
            <div class="the-affiliate__container">

              <div class="the-affiliate__benefits" @click="toggleWindow">
                <e-affiliate-benefit :benefits="benefits"></e-affiliate-benefit>
              </div>

              <div class="the-affiliate__copy">
                <span data-test="affiliateCopy">{{affiliateLink}}</span>
                <e-copy-text
                  title="Your affiliate link"
                  :hash="affiliateLink"
                  :icon="iconLink"
                  btnName="copy link"
                  mod
                >
                </e-copy-text>
              </div>
            </div>
          </template>
          <template slot="footer">
             <e-heading
              title="Download <br /> our banners"
              subtitle="25 banners, from 100x100px square <br /> banners to 780x400 skyscrappers."
              :icon="downloadBIcon"
              :btns="true"
            >
              <a href="/static/download/ethermium-banners.zip">
                <e-button
                  fill="green"
                  padding="primary"
                  adaptiveMod="primary"
                  iconSize="24"
                  :icon="iconDownload"
                >
                  DOWNLOAD our banners
                </e-button>
              </a>
            </e-heading>
          </template>
        </e-bottom-layout>
      </div>
    </transition-content>

    <transition-content>
      <div class="the-affiliate__statistics" v-if="statisticWindow">
        <div class="the-affiliate__head-title">
          <div class="the-affiliate__back">
            <e-button-back
              :icon="iconWallet"
              @buttonOnClick="toggleWindow"
            />
          </div>
          <e-heading
            title="Affiliate Statistics"
            :hasContent="true"
          >
            <div v-if="affiliateReferals">
              Total earned:
              <div class="the-affiliate__total-list">
                <e-total-earned-list
                  :selected-token-contract.sync="selectedToken"
                  :list="affiliateTokens"
                  @update:selectedTokenContract="updateSelectedTokenContract"
                />
              </div>
            </div>
          </e-heading>
        </div>
        <div class="the-affiliate__data">
          <div class="the-affiliate__title">
            <e-items-title
              title="AFFILIATE wallets created"
              :counter="affiliateReferals"
              :icon="iconWallet"
            />
          </div>
          <e-affiliate-stats-table :data-set="affiliateTradesFiltered" v-if="affiliateReferals" />
        </div>
      </div>
    </transition-content>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import forEach from 'lodash/forEach';
import { getAffiliatesAddressesCount } from '@/api/backend';

import EAffiliateBenefit from '@/components/EAffiliateBenefit';
import EButton from '@/components/EButton';
import ECopyText from '@/components/ECopyText';
import EHeading from '@/components/EHeading';
import EAffiliateStatsTable from '@/components/EAffiliateStatsTable';
import EItemsTitle from '@/components/EItemsTitle';
import ECircleIcon from '@/components/ECircleIcon';
import EButtonBack from '@/components/EButtonBack';
import ETotalEarnedList from '@/components/ETotalEarnedList';
import EBottomLayout from '@/components/EBottomLayout';

import iconLink from '@/icons/e-icon_affiliate-link.svg';
import iconDownload from '@/icons/e-icon_download_cloud.svg';
import affiliateIcon from '@/icons/e-icon_affiliate_heading.svg';
import downloadBIcon from '@/icons/e-icon_download_b.svg';
import iconWallet from '@/icons/e-icon_wallet.svg';
import iconClick from '@/icons/e-icon_target.svg';
import iconReferrals from '@/icons/e-icon_people.svg';
import iconEarned from '@/icons/e-icon_earned.svg';

import TransitionContent from '@/containers/transitions/TransitionContent';

export default {
  name: 'the-affiliate',

  data() {
    return {
      iconLink,
      iconDownload,
      affiliateIcon,
      downloadBIcon,
      iconWallet,
      selectedToken: '0x41951ff00c9a42a5944f5e1281d1e534c1b3b93f',
      affiliateTokens: [],

      // Количество афилированных с активным адресом адресов (пользователей зарегистрировавшихся по ссылке)
      affiliateReferals: 0,

      // Оторажать основное окно или окно статистики
      statisticWindow: false,
      affiliateTradesFiltered: [],
    };
  },

  computed: {
    ...mapGetters({
      affiliateClicks: 'affiliate/affiliateClicks',
      affiliateEth: 'affiliate/affiliateEth',
      affiliateTrades: 'affiliate/affiliateTrades',
      allTotalEarned: 'affiliate/allTotalEarned',
      isLogin: 'wallets/isLogin',
      tokenByContract: 'tokens/tokenByContract',
      walletAddress: 'wallets/activeWalletAddress',
      theme: 'ui/getTheme',
    }),

    affiliateLink() {
      return `${location.origin}/a/${this.walletAddress}`;
    },

    benefits() {
      return [
        {
          icon: iconClick,
          value: this.affiliateClicks,
          title: 'Clicks',
        },
        {
          icon: iconReferrals,
          value: this.affiliateReferals,
          title: 'Referals',
        },
        {
          icon: iconEarned,
          value: 0,
          title: 'ETH Earned',
        },
      ];
    },
  },

  watch: {
    affiliateTrades() {
      const affiliateTokens = [{ contract: 'all', shortName: 'All' }];

      const data = this.affiliateTrades;

      for (let i = 0; i < data.length; i += 1) {
        for (let j = 0; j < data[i].affiliates.length; j += 1) {
          const tokenAddress = data[i].affiliates[j].tokenAddress;

          const tokenIndex = affiliateTokens.findIndex(item => item.contract === tokenAddress);
          if (tokenIndex === -1) {
            affiliateTokens.push({
              contract: tokenAddress,
              value: data[i].affiliates[j].amount,
              shortName: this.tokenByContract(tokenAddress).shortName,
            });
          } else {
            affiliateTokens[tokenIndex].value = affiliateTokens[tokenIndex].value.plus(data[i].affiliates[j].amount);
          }
        }
      }

      this.affiliateTokens = affiliateTokens.map((item) => {
        if (item.value && item.contract) {
          const decimals = this.tokenByContract(item.contract).decimals;
          return {
            ...item,
            value: item.value.shiftedBy(-decimals),
          };
        }

        return item;
      });

      this.affiliateTradesFiltered = this.updateSelectedTokenContract('all');
    },

    walletAddress(newAdress, oldAdress) {
      if (newAdress !== oldAdress) {
        this.getAffiliateData();
      }
    },
  },

  async created() {
    this.affiliateReferals = await getAffiliatesAddressesCount(this.walletAddress);
    this.benefits[2].value = this.affiliateEth;
  },

  mounted() {
    this.getAffiliateData();
  },

  methods: {
    getAffiliateData() {
      // Обновление информации о количестве аффилированных кликов
      if (this.isLogin) {
        this.$store.dispatch('affiliate/getAffiliateClicksCount');
        this.$store.dispatch('affiliate/getAffiliateTrades');
      }
    },

    toggleWindow() {
      this.statisticWindow = !this.statisticWindow;
    },

    updateSelectedTokenContract(tokenAddress) {
      if (tokenAddress === 'all') {
        this.affiliateTradesFiltered = this.affiliateTrades;
        return;
      }

      const filtered = [];

      forEach(this.affiliateTrades, (item) => {
        const affiliates = [];
        forEach(item.affiliates, (affiliateItem) => {
          if (affiliateItem.tokenAddress === tokenAddress) {
            affiliates.push(affiliateItem);
          }
        });

        if (affiliates.length) {
          filtered.push({
            ...item,
            affiliates,
          });
        }
      });

      this.affiliateTradesFiltered = filtered;
    },
  },

  components: {
    EAffiliateBenefit,
    EButton,
    ECopyText,
    EHeading,
    EAffiliateStatsTable,
    EItemsTitle,
    ECircleIcon,
    EButtonBack,
    ETotalEarnedList,
    EBottomLayout,
    TransitionContent,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-affiliate {
  &__program {
    height: 100%;
  }

  &__container {
    margin-top: 58px;

    @include r(1200) {
      margin-top: 27px;
    }
  }

  &__statistics {
    padding-left: 17px;
    padding-right: 17px;

    @include r(1200) {
      padding-left: 0;
      padding-right: 0;
    }
  }

  &__benefits {
    margin-bottom: 20px;
  }

  &__copy {
    padding: 48px 0 59px 89px;

    @include r(1200) {
      padding-left: 36px;
      padding-bottom: 42px;
    }
  }

  &__btn {
    text-align: center;
  }

  &__title {
    padding: 65px 0 48px;

    @include r(1200) {
      padding: 44px 0 18px;
    }
  }

  &__head-title {
    display: flex;
  }

  &__back {
    margin-top: -8px;
  }

  &__footer {
    @include r(1200) {
      padding-left: 14px;
    }
  }

  &__total-list {
    display: inline-block;
    margin-left: 10px;
  }

  &__dark {
    .e-heading__title {
      color: #fff;
    }

    .e-heading__subtitle {
      color: $color-dark-grey;
    }

    .e-affiliate-benefit__item {
      background: #232323;
      box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.14);
    }

    .e-affiliate-benefit__value,
    .e-affiliate-benefit__title {
      color: $color-blue-dark-mode;
    }

    .e-affiliate-benefit__icon .e-icon svg {
      fill: $color-blue-dark-mode;
    }

    .e-copy-text__hash {
      color: #fff;
    }

    .e-copy-text__title {
      color: #8D9098;
    }

    .e-copy-text__type {
      color: #232323;
    }

    .e-copy-text__icon,
    .e-button_fill_white-circle,
    .e-circle-icon {
      background: #232323;
      box-shadow: 0 12px 24px rgba(0, 0, 0, 0.14);
    }

    .e-circle-icon .e-icon svg {
      fill: #8D9098;
    }

    .e-button_fill_white-circle:before {
      box-shadow: 0 12px 24px rgba(0, 0, 0, 0.14);
    }

    .e-button_fill_white,
    .e-copy-text__btn_copy .e-button:hover {
      background: #232323;
      color: $color-blue-dark-mode;

      &::before {
        box-shadow: 0 5px 9px rgba(0, 0, 0, 0.14);
      }
    }

    .e-button_fill_white:hover {
      box-shadow: none;
    }

    .e-bottom-layout__footer_border-top:before {
      display: none;
    }

    .e-button_fill_green {
      color: #fff;
    }

    .e-button_fill_green .e-icon svg {
      fill: #fff;
    }

    .e-heading__content {
      color: #8D9098;
    }

    .e-total-earned-list__field,
    .e-total-earned-list__item.is-current .e-total-earned-list__token {
      color: $color-blue-dark-mode;
    }

    .e-total-earned-list__token {
      color: #8D9098;
    }

    .e-total-earned-list__item {
      border-color: #393939;
    }

    .e-total-earned-list__item:hover .e-total-earned-list__token,
    .e-total-earned-list__item.is-current:hover .e-total-earned-list__token {
      color: #bcc0cb;
    }

    .e-total-earned-list__arrow .e-icon svg,
    .e-total-earned-list__current .e-icon svg {
      fill: $color-blue-dark-mode;
    }

    .e-total-earned-list__dropdown {
      background: #232323;
    }

    .e-total-earned-list__inner {
      &::-webkit-scrollbar-track {
        background-color: #212121;
      }

      &::-webkit-scrollbar-thumb {
        background-color: $color-dark-grey;
      }
    }

    .e-items-title:before {
      background-color: #232323;
    }

    .e-items-title__textual  {
      background: #232323;
      color: $color-dark-grey;
    }

    .e-items-title__icon .e-icon svg {
      fill: $color-dark-grey;
    }

    .e-affiliate-stats__table {
      color: $color-dark-grey;
    }

    .e-affiliate-stats__traded-amount,
    .e-affiliate-stats__affiliate-amount {
      color: #bcc0cb;
    }

    .e-affiliate-stats__traded-value .e-big-number__fraction,
    .e-affiliate-stats__affiliate-value .e-big-number__fraction {
      opacity: .5;
    }
  }
}

[data-test="affiliateCopy"] {
  display: none;
}
</style>




