<template>
  <div class="e-affiliate-stats">
    <table class="e-affiliate-stats__table">
      <thead class="e-affiliate-stats__thead">
        <tr>
          <th class="e-affiliate-stats__th e-affiliate-stats__head-date">Created date</th>
          <th class="e-affiliate-stats__th e-affiliate-stats__head-adress">Address</th>
          <th class="e-affiliate-stats__th e-affiliate-stats__head-traded">Traded</th>
          <th class="e-affiliate-stats__th e-affiliate-stats__head-affiliate">Affiliate</th>
        </tr>
      </thead>
      <tbody class="e-affiliate-stats__tbody">
        <tr
          class="e-affiliate-stats__tr"
          v-for="(content, i) in dataSet"
          :key="i"
        >
          <td class="e-affiliate-stats__td e-affiliate-stats__timestamp">{{dateToString(content.createdAt)}}</td>
          <td class="e-affiliate-stats__td e-affiliate-stats__address">{{content.transactionHash}}</td>

          <!-- BEGIN: Отображение объемов ордеров -->
          <td class="e-affiliate-stats__td e-affiliate-stats__traded e-affiliate-stats__data">
            <div
              class="e-affiliate-stats__traded-amount e-affiliate-stats__data-amount"
              v-for="(traded, i) in content.traded"
              :key="i"
            >
              <span class="e-affiliate-stats__traded-value e-affiliate-stats__data-value">
                <e-big-number
                  :value="traded.amount"
                  :decimals="tokenByContract(traded.tokenAddress).decimals"
                  :decimalPlaces="3"
                />
              </span>
              <span class="e-affiliate-stats__traded-token e-affiliate-stats__data-token">
                {{tokenByContract(traded.tokenAddress).shortName}}
              </span>
            </div>
          </td>
          <!-- END: Отображение объемов ордеров -->

          <!-- BEGIN: Отображение коммисий по ордерам -->
          <td class="e-affiliate-stats__td e-affiliate-stats__affiliate e-affiliate-stats__data">
            <div
              class="e-affiliate-stats__affiliate-amount e-affiliate-stats__data-amount"
              v-for="(affiliate, i) in content.affiliates"
              :key="i"
            >
              <span class="e-affiliate-stats__affiliate-value e-affiliate-stats__data-value">
                <e-big-number
                  :value="affiliate.amount"
                  :decimals="tokenByContract(affiliate.tokenAddress).decimals"
                  :decimalPlaces="8"
                />
              </span>
              <span class="e-affiliate-stats__affiliate-token e-affiliate-stats__data-token">
                {{tokenByContract(affiliate.tokenAddress).shortName}}
              </span>
            </div>
          </td>
          <!-- END: Отображение коммисий по ордерам -->
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
// Packeges
import { mapGetters } from 'vuex';

// Constans and helpers
import { dateToString } from '@/util/dates';

// Components
import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';

export default {
  name: 'e-affiliate-stats-table',

  props: {
    dataSet: {
      type: Array,
      default: () => [],
    },
  },

  computed: {
    ...mapGetters({
      tokenByContract: 'tokens/tokenByContract',
    }),
  },

  created() {
    Object.assign(this, {
      dateToString,
    });
  },

  components: {
    EIcon,
    EBigNumber,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-affiliate-stats {
  &__table {
    width: 100%;
    color: #0d105b;
    font-size: 16px;
    font-weight: 400;
    line-height: 21px;

    @include r(1450) {
      font-size: 14px;
    }

    @include r(1300) {
      font-size: 12px;
    }
  }

  &__th {
    color: rgba(13, 16, 91, .2);
    font-size: 20px;
    font-weight: 400;
    line-height: 40px;
    text-align: left;

    @include r(1200) {
      font-size: 12px;
      white-space: nowrap
    }
  }

  &__head-traded {
    text-align: right;
  }

  &__head-affiliate {
    text-align: right;
  }

  &__td {
    padding-top: 15px;
    padding-bottom: 20px;
    vertical-align: top;

    @include r(1300) {
      padding-top: 9px;
    }
  }

  &__timestamp {
    width: 20%;
    font-size: 12px;

    @include r(1300) {
      font-size: 10px;
    }
  }

  &__address {
    width: 42%;
  }

  &__data {
    width: 19%;
    text-align: right;
  }

  &__data-amount {
    position: relative;
    margin-bottom: 18px;
    font-weight: 400;

    &:last-child {
      margin-bottom: 0;
    }

    @include r(1300) {
      margin-bottom: 8px;
    }
  }

  &__data-token {
    position: relative;
    top: -1px;
    font-size: 12px;
    font-weight: 400;

    @include r(1300) {
      font-size: 10px;
    }
  }

  &__traded-value {
    & .e-big-number__fraction {
      opacity: 0.2;
    }
  }

  &__affiliate-value {
    & .e-big-number__fraction {
      opacity: 0.2;
    }
  }

  &__traded-amount {
    color: #0d105b;
  }

  &__affiliate-amount {
    color: #0123E7;
  }

  &__refresh {
    margin-top: 30px;
    margin-bottom: 10px;
    text-align: center;

    @include r(1300) {
      margin-top: 0;
    }
  }

  &__refresh-btn {
    display: inline-block;
    color: #1fce43;
    font-size: 16px;
    font-weight: 400;
    transition: $transition-time;

    .e-icon {
      position: relative;
      display: inline-block;
      top: 4px;
      width: 30px;
      height: 21px;
      margin-right: 8px;

      svg {
        fill: #1fce43;
        top: 50%;
        left: 50%;
        transition: $transition-time;
        transform: translate(-50%, -50%);
      }

      @include r(1300) {
        width: 22px;
      }
    }

    &:hover {
      color: $color-blue;

      .e-icon svg {
        fill: $color-blue;
      }
    }

    @include r(1300) {
      font-size: 14px;
    }
  }
}
</style>



