<template>
  <div class="e-affiliate-benefit">
    <div class="e-affiliate-benefit__item"
      v-for="(item, i) in benefits"
      :key="i"
    >
      <div class="e-affiliate-benefit__icon">
        <e-icon :src="item.icon" v-if="item.icon"></e-icon>
      </div>
      <div class="e-affiliate-benefit__value">{{item.value}}</div>
      <div class="e-affiliate-benefit__title">{{item.title}}</div>
    </div>
  </div>
</template>

<script>
import EIcon from '@/components/EIcon';

export default {
  name: 'e-affiliate-benefit',

  props: {
    benefits: {
      type: Array,
      default: () => [],
    },
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-affiliate-benefit {
  $this: &;

  display: flex;
  justify-content: space-between;

  &__item {
    cursor: pointer;
    padding-top: 13px;
    padding-bottom: 20px;
    text-align: center;
    background: #FFFFFF;
    box-shadow: 0 3px 10px 0 rgba(148,165,180,0.14);
    border-radius: 10px;
    transition: .3s ease;
    width: 100%;

    &:not(:last-of-type) {
      margin-right: 54px;
    }

    @include r(1200) {
      padding-bottom: 18px;

      &:not(:last-of-type) {
        margin-right: 34px;
      }
    }

    &:last-of-type {
      margin-right: 0;
    }

    &:hover {
      background-color: $color-blue;

      #{$this}__icon {
        .e-icon svg {
          fill: #fff;
        }
      }

      #{$this}__value {
        color: $color-green;
      }

      #{$this}__title {
        color: #fff;
      }
    }
  }

  &__icon {
    .e-icon {
      display: inline-block;
      width: 25px;
      height: 25px;

      svg {
        fill: #0123E7;
        transition: .3s ease;
      }

      @include r(1200) {
        height: 22px;
        width: 22px;
      }
    }
  }

  &__value {
    margin-top: 12px;
    color: #09168a;
    font-size: 31px;
    letter-spacing: 2.38px;
    transition: .3s ease;

    @include r(1200) {
      font-size: 28px;
      margin-top: 6px;
    }
  }

  &__title {
    margin-top: 15px;
    color: #0123e7;
    font-size: 11px;
    font-weight: 700;
    letter-spacing: 0.85px;
    text-transform: uppercase;
    transition: .3s ease;

    @include r(1200) {
      font-size: 9px;
      margin-top: 18px;
    }
  }
}
</style>



