<template>
  <div
    class="e-total-earned-list"
    :class="[dropdown ? 'is-active' : '']"
    v-click-outside="handleClickOutside"
  >
    <div
      data-test="totalEarnedListField"
      class="e-total-earned-list__field"
      @click="dropdown = !dropdown"
    >
      <div class="e-total-earned-list__head-icon">
        <e-token-icon
          :contract="selectedToken.contract"
          type="small"
          v-if="selectedToken.value"
        />
      </div>
      <e-big-number :value="selectedToken.value" :decimalPlaces="9" v-if="selectedToken.value" />
      {{selectedToken.shortName}}
      <div class="e-total-earned-list__arrow">
        <e-icon :src="iconArrow"/>
      </div>
    </div>

    <transition-dropdown>
      <div class="e-total-earned-list__dropdown" v-show="dropdown">
        <div class="e-total-earned-list__inner">
          <div
            v-for="{ contract, name, shortName, value } of list"
            :key="shortName"
            :class="{'is-current': selectedToken.contract === contract }"
            class="e-total-earned-list__item"
            data-test="totalEarnedListItem"
            @click="handleClick(contract)"
          >
            <div class="e-total-earned-list__icon">
              <e-token-icon
                :contract="contract"
                type="small"
                v-if="value"
              />
            </div>
            <div class="e-total-earned-list__token">
              <e-big-number :value="value" :decimalPlaces="8" v-if="value" />
              {{shortName}}
            </div>
            <div class="e-total-earned-list__current">
              <e-icon :src="iconCheckMarker"/>
            </div>
          </div>
        </div>
      </div>
    </transition-dropdown>
  </div>
</template>

<script>
import iconCheckMarker from '@/icons/e-icon_check-marker.svg';
import iconArrow from '@/icons/e-icon_arrow_downer.svg';

import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';
import ETokenIcon from '@/components/ETokenIcon';

import TransitionDropdown from '@/containers/transitions/TransitionDropdown';

export default {
  name: 'e-total-earned-list',

  props: {
    selectedTokenContract: {
      type: String,
      required: true,
    },

    list: {
      type: Array,
      required: true,
    },
  },

  data() {
    return {
      iconCheckMarker,
      iconArrow,
      dropdown: false,

      settings: {
        maxScrollbarLength: 48,
        minScrollbarLength: 48,
        wheelSpeed: 1,
        wheelPropagation: true,
      },
    };
  },

  created() {
    const selectedNotExist = !this.list.some(t => t.contract === this.selectedTokenContract);
    if (selectedNotExist && this.list.length) {
      this.$emit('update:selectedTokenContract', this.list[0].contract);
    }
  },

  computed: {
    selectedToken() {
      return this.list.find(({ contract }) => contract === this.selectedTokenContract) || {};
    },
  },

  methods: {
    handleClick(contract) {
      this.dropdown = false;
      this.$emit('update:selectedTokenContract', contract);
    },

    handleClickOutside() {
      this.dropdown = false;
    },
  },

  components: {
    EBigNumber,
    EIcon,
    ETokenIcon,
    TransitionDropdown,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-total-earned-list {
  $this: &;

  position: relative;
  z-index: 12;

  &__field {
    position: relative;
    padding-left: 23px;
    padding-right: 15px;
    padding-top: 1px;
    color: $color-blue;
    cursor: pointer;
    user-select: none;
    transition: $transition-time;

    .e-big-number {
      font-size: 16px;
      font-weight: 500;

      &__fraction {
        opacity: .2;
      }
    }

    &:hover {
      color: $color-green-bright;

      #{$this}__arrow svg {
        fill: $color-green-bright;
      }
    }
  }

  &__head-icon {
    position: absolute;
    top: 3px;
    left: 0;
    width: 15px;
    height: 15px;
  }

  &__arrow {
    position: absolute;
    top: 50%;
    margin-top: -4px;
    right: 0;
    width: 8px;
    height: 5px;
    transition: .3s;
    transform: rotate(0);

    .e-icon svg {
      fill: $color-blue;
      transition: $transition-time;
    }

    .is-active & {
      transform: rotate(180deg);
    }
  }

  &__dropdown {
    position: absolute;
    top: calc(100% + 9px);
    left: -21px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 3px 10px rgba(#94A5B4, .14);
    min-width: 264px;
    padding-right: 4px;
    padding-top: 10px;
    padding-bottom: 10px;
    box-sizing: border-box;
    padding-left: 7px;
  }

  &__item {
    padding-left: 37px;
    padding-top: 11px;
    padding-right: 30px;
    padding-bottom: 17px;
    position: relative;
    cursor: pointer;
    border-bottom: 2px solid $color-light-grey;

    &:not(:last-child) {
      margin-bottom: 9px;
    }

    &:hover {
      #{$this}__token {
        color: $color-blue;
      }
    }

    &.is-current {
      #{$this}__icon img {
        color: $color-blue;
      }

      #{$this}__current {
        opacity: 1;
      }

      #{$this}__token {
        color: $color-blue;
      }
    }
  }

  &__current {
    position: absolute;
    top: 50%;
    right: 10px;
    margin-top: -10px;
    width: 15px;
    height: 13px;
    transition: .3s;
    opacity: 0;

    .e-icon svg {
      fill: $color-green;
    }
  }

  &__token {
    font-size: 14px;
    color: $color-dark-blue;
    font-weight: 400;
    transition: $transition-time;

    .e-big-number__fraction {
      opacity: .5;
    }
  }

  &__inner {
    max-height: 220px;
    position: relative;
    padding-right: 5px;
    overflow-y: auto;

    &::-webkit-scrollbar {
      width: 2px;
    }

    &::-webkit-scrollbar-track {
      background: $color-violet;
      border-radius: 2px;
    }

    &::-webkit-scrollbar-thumb {
      background: $color-blue;
      border-radius: 2px;
    }
  }

  &__icon {
    height: 12px;
    width: 11px;
    position: absolute;
    top: 50%;
    margin-top: -13px;
    left: 16px;
  }
}
</style>




