<template>
  <div class="e-bottom-layout">
    <div
      class="e-bottom-layout__body"
      :class="bodyClass"
    >
      <slot name="body"></slot>
    </div>
    <div
      class="e-bottom-layout__footer"
      :class="[
        borderTop ? 'e-bottom-layout__footer_border-top' : '',
      ]"
    >
      <slot name="footer"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'e-bottom-layout',

  props: {
    borderTop: {
      type: Boolean,
      default: false,
    },
    bodyClass: {
      type: String,
      default: '',
    },
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-bottom-layout {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;

  &__footer_border-top {
    padding-top: 80px;
    padding-bottom: 20px;
    position: relative;

    &:before {
      background-color: #F8F9FC;
      content: '';
      height: 2px;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }
  }
}
</style>



